\n                    });\r\n\r\n                    localStorage.setItem( \"approval_line_users\", this.approval_line_users );\r\n\r\n                    console.log(\"__vue__approval_line_users__\", this.approval_line_users);\r\n                    alert(\"test\");\r\n                    this.$emit('close');\r\n                })\r\n                .catch( err => {\r\n                    console.error(err);\r\n                });\r\n\r\n\r\n            }\r\n\r\n        }\r\n    }\r\n</script>\r\n\r\n\r\n\r\n\r\n\r\n<style scoped>\r\n.mydrag-element {\r\n    width: 300px;\r\n    border: solid gray 1px;\r\n}\r\n.c-emtpy-box {\r\n    width:10px;\r\n    height:100px;\r\n}\r\n</style>"],"sourceRoot":""}]);
